import { DataSourceTypeEnum } from "../type";
import axios from "axios";
import { useDataSourceStore } from "./store";
import globalBus from "../gobalBus";
import { GlobalChannel } from "../constant";

const restfulAxiosInstace = axios.create({
  timeout: 5000,
});

restfulAxiosInstace.interceptors.response.use(
  response => {
    return response.data;
  },
  // error => {
  //   console.log(error);
  //   return error;
  // }
)

function processRestfulQuery(query) {
  const { options = {} } = query;
  const { url, method } = options;
  const fn = () => {
    return restfulAxiosInstace(url, {
      method: method.toUpperCase(),
    }).then(data => {
      useDataSourceStore.getState().updateQueryMeta(query.id, { api: { data } });
      globalBus.publish(GlobalChannel.EDITOR_QUERY, {
        type: 'complete',
        source: query.id,
        timestamp: performance.now(),
      })
      return data;
    });
  }

  return fn;
}

export function processQuery(query) {
  const { type } = query;

  let result: any = null;
  switch(type) {
    case DataSourceTypeEnum.REST接口:
      result = processRestfulQuery(query);
      break;
    default:
      break;
  }
  return result;
}