import axios from 'axios';

const base = axios.create({
  baseURL: 'http://47.109.16.158:3000',
  timeout: 5000,
});

base.interceptors.response.use(
  response => {
    if (response.status < 200 && response.status >= 400) {
      return {
        statusCode: -1,
        statusMessage: `请求失败 ${response.status} ${response.statusText}`,
        data: response.data,
      }
    }
    return response.data;
  },
  error => {
    console.error("请求错误", error);
    return {
      statusCode: -2,
      statusMessage: '请求错误',
      data: error,
    }
  }
);

export default base;