import { create } from "zustand";
import { DataSourceTypeEnum } from "../type";
import { devExpose, generateShortId, runAsync } from "../misc";
import { processQuery } from "./utils";
import { updateExpressionContext } from "../expression";
import { mapValues } from "lodash-es";
import globalBus from "../gobalBus";
import { GlobalChannel } from "../constant";

const getDefaultQuery = () => {
  return {
    type: DataSourceTypeEnum.REST接口,
    name: 'restful接口',
    id: generateNewQueryId(),
    options: {
      method: 'get',
      url: '',
      defaultRun: false,
    }
  }
}

export function initialQueries(queries: any) {
  const queryHandlers = mapValues(queries, (query => processQuery(query)));
  useDataSourceStore.setState({ 
    queries,
    queryHandlers,
  });
  // 发起自动执行的请求
  for (const query of Object.values(queries) as any) {
    if (query.options.defaultRun) {
      queryHandlers[query.id]();
    }
  }
}

export const useDataSourceStore = create<any>((set, get) => ({
  queries: {},
  selectedQueryId: '',
  queryHandlers: {},
  setQueryOptions: (queryId, options, replace = false) => set(state => {
    const { queries, queryHandlers } = state;
    const query = queries[queryId];
    const prevOptions = query.options;
    const nextOptions = replace ? options : { ...prevOptions, ...options };
    query.options = nextOptions;
    const queryHandler = processQuery(query);
    queryHandlers[queryId] = queryHandler;
    queries[queryId] = { ...query };
    return {
      queries,
    }
  }),
  updateQueryMeta: (id, newQuery, replace = false) => {
    const { queries } = get();
    const query = queries[id];
    let queryToUpdate = query;
    if (replace) {
      queryToUpdate = newQuery;
    } else {
      queryToUpdate = Object.assign({}, query, newQuery);
    }
    queries[id] = queryToUpdate;
    set({
      queries,
    })
  },
  addQuery: () => {
    const { queries } = get();
    const newQuery = getDefaultQuery();
    queries[newQuery.id] = newQuery;
    set({ queries: { ...queries } });
    updateExpressionContext();
    return newQuery.id;
  },
  deleteQuery: (id) => {
    const { queries, queryHandlers } = get();
    const query = queries[id];
    delete queries[id];
    delete queryHandlers[id];
    runAsync(() => {
      globalBus.publish(GlobalChannel.EDITOR_QUERY, {
        type: 'delete',
        source: id,
        timestamp: Date.now(),
      })
    });
    set({ 
      queries: { ...queries },
      queryHandlers: { ...queryHandlers },
    })
    return query;
  }
}));

function generateNewQueryId() {
  const { queries } = useDataSourceStore.getState();
  const queryIds = Object.keys(queries);
  const defaultIdPattern = /^query(\d+)$/;
  let maxIdx = 0;
  queryIds.forEach(id => {
    const match = defaultIdPattern.exec(id);
    if (!match) {
      return;
    }
    maxIdx = Math.max(maxIdx, Number(match[1]));
    defaultIdPattern.lastIndex = 0;
  });
  return `query${maxIdx + 1}`;
}

setTimeout(() => {
  devExpose('__DATA_SOURCE__', () => useDataSourceStore.getState());
})